@charset "utf-8";

$baseFontSize:12px;
@import "_mixin";
@import url("http://fonts.googleapis.com/earlyaccess/notosanskr.css");

*{ margin:0; padding:0; }
body{ @include setFont($baseFontSize, 'Noto Sans KR'); color:#555; }
img, fieldset{ border:0 }
blockquote, q{ quotes:none; }
hr{ border-bottom:1px solid #e2e2e2; }
strong{ color:#000 }
p, ul, ol, li, dl, dt, dd, form{ list-style:none; border:none }
a, img, input, select, label{ vertical-align:middle }
a{
	text-decoration:none; color:#666;
	&:hover{ color:#000 }
}
// form reset
legend, figcaption{ display:none; }
textarea{ padding:2px; resize:none; }
input[type=text],
input[type=password],
input[type=date],
input[type=tel],
input[type=number],
input[type=email],
input[type=url],
input[type=file],
textarea{ padding:5px 10px; border:1px solid #ccc;}
label ,input{ margin:-2px 4px 0 0; }
// table reset
table{ width:100%; border-collapse:collapse; border-spacing:0; table-layout:fixed; font-size:12px; }
td{ overflow:hidden; }
caption{ visibility:hidden; width:0; height:0; overflow:hidden; font-size:0; }
.guide-box{
	padding:15px; border:1px solid #ccc; margin:15px; position:relative; z-index:0;
	&:after{ content:''; display:block; clear:both; }
}
.line{ border-top:1px solid #ccc; margin:20px 0; }
.guide-tit{ background:#eee; border-bottom:1px solid #ccc; text-align:center; padding:10px; }
.btn{ display:inline-block; font-size:inherit; border-radius:5px; padding:5px 10px; border:1px solid #ccc; margin:0 2px; background:#fff; line-height:1; vertical-align:middle;}
.btn-wrap{margin:10px -2px;}